// ---- Deterministic helpers (multisig/art token) ----
export async function jcsCanonicalize(json: unknown): Promise<string> {
  await initWasm();
  const raw = (wasmPkg as any).jcs_canonicalize(JSON.stringify(json));
  const out = typeof raw === 'string' ? JSON.parse(raw) : raw;
  if (out?.error) throw new Error(out.error);
  return out.canonical as string;
}

