// ---- Veritas Block Preimage (NEW v2 JCS canonical form) ----
// We migrate block signing to a canonical (JCS) JSON similar to other preimages.
// Legacy (v1) blocks used a hand-crafted JSON.stringify of a plain object with fixed insertion order.
// This helper produces a canonical JSON string and msg_b64u = b64url(UTF-8(canonical JSON)).
// NOTE: We intentionally EXCLUDE signature, encryptionKeyHash, and any runtime-only fields.
// Fields included (ordered via JCS lexical ordering):
//   contentType, dataHash, index, metadataHash, ownerPublicKey, previousHash, signatureAlg, timestamp, version
// index & timestamp MUST be strings representing decimal integers (BigInt safe)  caller responsibility.
export interface BlockPreimageInput {
  index: string | number | bigint;
  timestamp: string | number | bigint;
  previousHash: string;
  dataHash: string;
  metadataHash: string;
  signatureAlg: string; // e.g. 'dilithium2'
  ownerPublicKey: string; // Dilithium public key (base64url)
  contentType?: string; // default 'application/json'
  version: number; // block format / protocol version
}

export async function buildBlockPreimage(block: BlockPreimageInput): Promise<CanonicalOut> {
  await initWasm();
  // Normalize fields per spec
  const normalized = {
    contentType: block.contentType || 'application/json',
    dataHash: block.dataHash,
    index: block.index.toString(),
    metadataHash: block.metadataHash,
    ownerPublicKey: block.ownerPublicKey,
    previousHash: block.previousHash,
    signatureAlg: block.signatureAlg,
    timestamp: block.timestamp.toString(),
    version: block.version,
  };
  // Use JCS canonicalization via wasm jcs_canonicalize (already exposed through jcsCanonicalize helper)
  const canonical = await jcsCanonicalize(normalized);
  const msg_b64u = b64uEncode(new TextEncoder().encode(canonical));
  return { canonical, msg_b64u, msgB64u: msg_b64u };
}

export async function validateRoyalty(receiver: string, bps: number): Promise<boolean> {
  await initWasm();
  const raw = (wasmPkg as any).validate_royalty(receiver, bps);
  const out = typeof raw === 'string' ? JSON.parse(raw) : raw;
  if (out?.error) return false;
  return !!out.ok;
}

export async function verifyAttestations(msgB64u: string, attestations: Array<{ alg: string; publicKeyB64u: string; signatureB64u: string }>, allowedPublicKeys?: string[]): Promise<number> {
  await initWasm();
  const raw = (wasmPkg as any).verify_attestations(msgB64u, JSON.stringify(attestations), allowedPublicKeys ? JSON.stringify(allowedPublicKeys) : undefined);
  const out = typeof raw === 'string' ? JSON.parse(raw) : raw;
  if (out?.error) throw new Error(out.error);
  return Number(out.valid_count || 0);
}

// Utility functions
// Cross-env base64url helpers (browser + Node)
function _fromByteArray(bytes: Uint8Array): string {
  if (typeof btoa === 'function') {
    // Browser
    let s = '';
    for (let i = 0; i < bytes.length; i++) s += String.fromCharCode(bytes[i]);
    return btoa(s);
  }
  // Node
  return Buffer.from(bytes).toString('base64');
}

function _toByteArray(b64: string): Uint8Array {
  if (typeof atob === 'function') {
    const binary = atob(b64);
    const out = new Uint8Array(binary.length);
    for (let i = 0; i < binary.length; i++) out[i] = binary.charCodeAt(i);
    return out;
  }
  return new Uint8Array(Buffer.from(b64, 'base64'));
}

export function b64uEncode(data: Uint8Array): string {
  return _fromByteArray(data)
    .replace(/\+/g, '-')
    .replace(/\//g, '_')
    .replace(/=+/g, '');
}

export function b64uDecode(str: string): Uint8Array {
  const b64 = str.replace(/-/g, '+').replace(/_/g, '/').padEnd(Math.ceil(str.length / 4) * 4, '=');
  return _toByteArray(b64);
}

export function constantTimeEqual(a: Uint8Array, b: Uint8Array): boolean {
  if (a.length !== b.length) return false;
  let result = 0;
  for (let i = 0; i < a.length; i++) {
    result |= a[i] ^ b[i];
  }
  return result === 0;
}
